import React, {useState} from 'react';
import Apartment from '../assets/imgs/apartment.jpg'
import House from '../assets/imgs/house.jpg'
import Office from '../assets/imgs/office.jpg'
import Conditioner1 from '../assets/imgs/conditioner1.png'
import Conditioner2 from '../assets/imgs/conditioner2.png'
import Conditioner3 from '../assets/imgs/conditioner3.png'

const Quiz = () => {
  const [page, setPage] = useState(1);
  const count = 4;
  const arr = Array.from({ length: count }, (v, i) =>  i + 1); 
  console.log(arr);

  const isActive = (num) => (num === page ? 'active' : '')
  return (
    <form className='quiz'>
      <div className="quiz-pagination">
        <h5> {page}  {count}</h5>
        <div className="quiz-pagination-bullets">
          {
            arr.map(obj => <div key={obj} className={isActive(obj)}></div>)
          }
        </div>
      </div>

      {
        (page === 1) && 
        <>
          <div className="quiz-top">
            <h3>    ?</h3>
          </div>
          <div className="quiz-main">
            <button type='button' className='btn-6 me-4' disabled></button>
            <ul className="flex-1 list-unstyled row row-cols-3 gx-2 gx-sm-3 gx-xxl-4 gx-xxxl-5">
              <li>
                <label className='variant'>
                  <input type="radio" name='placement' hidden/>
                  <div className='variant-visual'>
                    <h3 className='text-center'></h3>
                    <img src={Apartment} alt="" />
                  </div>
                </label>
              </li>
              <li>
                <label className='variant'>
                  <input type="radio" name='placement' hidden/>
                  <div className='variant-visual'>
                    <h3 className='text-center'></h3>
                    <img src={House} alt="" />
                  </div>
                </label>
              </li>
              <li>
                <label className='variant'>
                  <input type="radio" name='placement' hidden/>
                  <div className='variant-visual'>
                    <h3 className='text-center'></h3>
                    <img src={Office} alt="" />
                  </div>
                </label>
              </li>
            </ul>
            <button type='button' className='btn-1 ms-4' onClick={() => setPage(2)}></button>
          </div>
        </>
      }

      {
        (page === 2) && 
        <>
          <div className="quiz-top">
            <h3>  </h3>
          </div>
          <div className="quiz-main">
            <button type='button' className='btn-6 me-4' onClick={() => setPage(1)}></button>
            <ul className="flex-1 list-unstyled row row-cols-2 row-cols-sm-3 g-3 gx-xxl-4 gx-xxxl-5 gy-xxl-4">
              <li>
                <label className='variant-2'>
                  <input type="radio" name='square' hidden/>
                  <div className='variant-2-visual'>
                    <h5 className='text-center mb-0'> 20 ..</h5>
                  </div>
                </label>
              </li>
              <li>
                <label className='variant-2'>
                  <input type="radio" name='square' hidden/>
                  <div className='variant-2-visual'>
                    <h5 className='text-center mb-0'> 30 ..</h5>
                  </div>
                </label>
              </li>
              <li>
                <label className='variant-2'>
                  <input type="radio" name='square' hidden/>
                  <div className='variant-2-visual'>
                    <h5 className='text-center mb-0'> 40 ..</h5>
                  </div>
                </label>
              </li>
              <li>
                <label className='variant-2'>
                  <input type="radio" name='square' hidden/>
                  <div className='variant-2-visual'>
                    <h5 className='text-center mb-0'> 50 ..</h5>
                  </div>
                </label>
              </li>
              <li>
                <label className='variant-2'>
                  <input type="radio" name='square' hidden/>
                  <div className='variant-2-visual'>
                    <h5 className='text-center mb-0'> 60 ..</h5>
                  </div>
                </label>
              </li>
              <li>
                <label className='variant-2'>
                  <input type="radio" name='square' hidden/>
                  <div className='variant-2-visual'>
                    <h5 className='text-center mb-0'> 70 ..</h5>
                  </div>
                </label>
              </li>
              <li>
                <label className='variant-2'>
                  <input type="radio" name='square' hidden/>
                  <div className='variant-2-visual'>
                    <h5 className='text-center mb-0'> 80 ..</h5>
                  </div>
                </label>
              </li>
              <li>
                <label className='variant-2'>
                  <input type="radio" name='square' hidden/>
                  <div className='variant-2-visual'>
                    <h5 className='text-center mb-0'> 100 ..</h5>
                  </div>
                </label>
              </li>
              <li>
                <label className='variant-2'>
                  <input type="radio" name='square' hidden/>
                  <div className='variant-2-visual'>
                    <h5 className='text-center mb-0'> 100 ..</h5>
                  </div>
                </label>
              </li>
            </ul>
            <button type='button' className='btn-1 ms-4' onClick={() => setPage(3)}></button>
          </div>
        </>
      }
      
      {
        (page === 3) && 
        <>
          <div className="quiz-top">
            <h3>    ?</h3>
          </div>
          <div className="quiz-main">
            <button type='button' className='btn-6 me-4' onClick={() => setPage(2)}></button>
            <ul className="flex-1 list-unstyled row row-cols-2 row-cols-sm-3 gx-3 gx-xxl-4 gx-xxxl-5 gy-4">
              <li>
                <label className='variant'>
                  <input type="radio" name='conditioner' hidden/>
                  <div className='variant-visual'>
                    <h3 className='text-center'> <br/>-</h3>
                    <img src={Conditioner1} className='object-fit-contain' alt=" -" />
                  </div>
                </label>
              </li>
              <li>
                <label className='variant'>
                  <input type="radio" name='conditioner' hidden/>
                  <div className='variant-visual'>
                    <h3 className='text-center'> <br/></h3>
                    <img src={Conditioner2} className='object-fit-contain' alt=" " />
                  </div>
                </label>
              </li>
              <li>
                <label className='variant'>
                  <input type="radio" name='conditioner' hidden/>
                  <div className='variant-visual'>
                    <h3 className='text-center'> <br/></h3>
                    <img src={Conditioner3} className='object-fit-contain' alt=" " />
                  </div>
                </label>
              </li>
            </ul>
            <button type='button' className='btn-1 ms-4' onClick={() => setPage(4)}></button>
          </div>
        </>
      }

      {
        (page === 4) && 
        <>
          <div className="quiz-top">
            <h3> 10%   </h3>
          </div>
          <div className="quiz-main last">
            <button type='button' className='btn-6' onClick={() => setPage(1)}> </button>
            <div className='content'>
              <h3>       </h3>
              <div className="d-md-flex fs-12 mb-2">
                <input type="tel" placeholder='+7___-___-__-__' className='mb-3 mb-md-0 me-md-4 me-lg-5'/>
                <input type="text" placeholder='' className='mb-3 mb-md-0 me-md-4 me-lg-5'/>
                <button type='button' className="btn-1 fw-6 mx-auto mx-md-0"></button>
              </div>
              <p>  ,     </p>
            </div>
          </div>
        </>
      }
    </form>
  );
};

export default Quiz;