import React from 'react';
import Container from 'react-bootstrap/Container';
import {Link, NavLink} from 'react-router-dom';
import useIsMobile from '../hooks/useIsMobile';
import { HiOutlineHome, HiOutlineSquares2X2, HiOutlineShoppingCart, HiOutlinePhone, HiOutlineUser } from "react-icons/hi2";
import Devs from './utils/Devs';

const Footer = () => {
  const {isMobile} = useIsMobile('991px');

  if(isMobile) {
    return (<footer className='mobile'>
      <Container className='h-100'>
        <nav>
          <ul>
            <li>
              <NavLink to="/" end>
                <HiOutlineHome/>
                <span></span>
              </NavLink>
            </li>
            <li>
              <NavLink to="/catalog">
                <HiOutlineSquares2X2/>
                <span></span>
              </NavLink>
            </li>
            <li>
              <NavLink to="/cart">
                <HiOutlineShoppingCart/>
                <span></span>
              </NavLink>
            </li>
            <li>
              <NavLink to="/contacts">
                <HiOutlinePhone/>
                <span></span>
              </NavLink>
            </li>
            <li>
              <NavLink to="/profile">
                <HiOutlineUser/>
                <span></span>
              </NavLink>
            </li>
          </ul>
        </nav>
      </Container>
    </footer>)
  } else { 
    return (
      <footer className='desktop'>
        <Container className='h-100 d-flex justify-content-between align-items-center'>
          <div>
            <ul className='mb-4'>
              <li>
                <Link to="/"></Link>
              </li>
              <li>
                <Link to="/"></Link>
              </li>
              <li>
                <Link to="/"></Link>
              </li>
              <li>
                <Link to="/"> </Link>
              </li>
              <li>
                <Link to="/"> </Link>
              </li>
              <li>
                <Link to="/"></Link>
              </li>
            </ul>
            <ul className='mb-4'>
              <li>
                <Link to="/"></Link>
              </li>
              <li>
                <Link to="/"></Link>
              </li>
              <li>
                <Link to="/"></Link>
              </li>
              <li>
                <Link to="/">-</Link>
              </li>
              <li>
                <Link to="/"> </Link>
              </li>
            </ul>
            <div><Link to="/" className='bb-05'> </Link></div>
          </div>
          <Devs/>
        </Container>
      </footer>
    );
  }
};

export default Footer;